.container {
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: aquamarine;
    background-image: url(/public/images/background.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    overflow-y: scroll;
}
.row {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 50px 0px;
}