.container {
    width: 40%;
    display: flex;
    height: 100%;
    flex-direction: column;
    background-color: palegoldenrod;
    align-items: center;
    padding-top: 20px;
    overflow-y: scroll;
}
.header {
    width: 80%;
    padding:10px;
    background-color: blue;
    color: #ffffff;
    height: fit-content;
    border-radius: 10px;
    margin-top: 20px;
}
.content {
    width: 80%;
    padding:10px;
}