.loginContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url('/public/images/bg.png'); /* Replace or remove */
    background-size: cover;
    padding: 1rem;
  }
  
  .loginBox {
    background: #fff9e6;
    border-radius: 15px;
    padding: 2rem;
    max-width: 400px;
    width: 100%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  }
  
  .loginButton {
    background-color: #7ec850;
    border: none;
  }
  
  .alternativeSignin {
    text-align: center;
    margin-top: 1rem;
  }
  
  .iconGroup > * {
    margin: 0 0.5rem;
    font-size: 20px;
    cursor: pointer;
  }
  
  .registerText {
    margin-top: 1rem;
    text-align: center;
  }
  