.custom-table .ant-table-thead > tr > th {
    background-color: #0000FF; /* header background */
    color: #ffffff;            /* header text color */
    font-weight: 700;
  }
.quantitycolumn {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.increaseItem {
  width:25px;
  height:25px;
  border-radius:100%;
  display:flex;
  justify-content:center;
  background-color:darkgreen;
  color:white;
  align-items:center;
}
.increaseItem:hover {
  cursor: pointer;
}
